body#tinymce {
  background-color: #fff;
  margin: 12px !important;
}

.g {

  &-section-subtitle {
    color: $green;
    text-align: center;
    font-family: $sans;
    font-style: normal;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0.2rem;

    @include breakpoint(medium) {
      font-size: 1.5rem;
      letter-spacing: 0.3rem;
      line-height: 1.9375rem; /* 129.167% */
    }

  }

  &-section-title {
    color: $black;
    display: block;
    font-family: $sans;
    font-size: 26px;
    font-weight: 800;
    margin: 0 0 30px;

    @include breakpoint(medium) {
      font-size: 36px;
    }

    @include breakpoint(xlarge) {
      font-size: 46px;
    }

  }

  &-section-narrow-p{
    display: block;
    max-width: 36.5rem;
    margin: auto;
  }

  &-section-intro {
    color: $black;
    display: block;
    font-family: $sans;
    font-size: 16px;
    font-weight: 400;
    margin: 0 0 0;
  }

  &-section-large-numbers {
    color: $green;
    display: block;
    font-family: $sans;
    font-size: 90px;
    font-weight: 300;
    line-height: 1;
    margin-bottom: 20px;
  }

  &-button.button {
    margin-right: 10px;
    background-color: transparent;
    border-radius: 2px;
    border: 1px solid $black;
    color: $black;
    display: inline-block;
    font-family: $sans;
    font-size: 14px !important;
    font-weight: 600;
    margin-top: 1rem;
    letter-spacing: 1.5px;
    padding: 8px 34px 8px 20px;
    position:relative;
    text-transform: uppercase;

    a {
      position: relative;
    }

    &:before {
      font-family: $fontawesome;
      content: "\f054";
      font-weight: 400;
      position: absolute;
       top: 8px;
       right: 14px;
      height: 30px;
      font-size: 14px;
      width: 8px;
    }

    i {
      margin-left: 14px;
      //font-size: 10px;
    }

    &:hover, &.active {
      background-color: darken($primary,7%);
      color: #fff;
    }

  }

  &-image--top-left{
    
    border-radius: 1rem;
    position: relative;
    right: unset;
    left: unset;
    margin: auto;
    display: block;
    transition: all 0.5s ease-in-out;

    @include breakpoint(xlarge) {
      position: absolute;
      top: 0;
      left:  -7rem;
    }

    @include breakpoint(xxlarge) {
      position: absolute;
      top: 0;
      left: -16px;
      
    }


  }

  &-image--top-right{
    
    position: relative;
    border-radius: 1rem;
    right: unset;
    left: unset;
    margin: auto;
    display: block;
    transition: all 0.5s ease-in-out;

    @include breakpoint(xlarge) {
      position: absolute;
      bottom: 0;
      right: -7rem;
    }

    @include breakpoint(xxlarge) {
      right: -16px;
      position: absolute;
      bottom: 0;
      
    }

  }


}