body#tinymce {
  background-color: #fff;
  margin: 12px !important;
}

.g-section-subtitle {
  color: #86AB3C;
  text-align: center;
  font-family: "Source Sans 3", sans-serif;
  font-style: normal;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
}
@media print, screen and (min-width: 40em) {
  .g-section-subtitle {
    font-size: 1.5rem;
    letter-spacing: 0.3rem;
    line-height: 1.9375rem; /* 129.167% */
  }
}
.g-section-title {
  color: #002B49;
  display: block;
  font-family: "Source Sans 3", sans-serif;
  font-size: 26px;
  font-weight: 800;
  margin: 0 0 30px;
}
@media print, screen and (min-width: 40em) {
  .g-section-title {
    font-size: 36px;
  }
}
@media screen and (min-width: 80em) {
  .g-section-title {
    font-size: 46px;
  }
}
.g-section-narrow-p {
  display: block;
  max-width: 36.5rem;
  margin: auto;
}
.g-section-intro {
  color: #002B49;
  display: block;
  font-family: "Source Sans 3", sans-serif;
  font-size: 16px;
  font-weight: 400;
  margin: 0 0 0;
}
.g-section-large-numbers {
  color: #86AB3C;
  display: block;
  font-family: "Source Sans 3", sans-serif;
  font-size: 90px;
  font-weight: 300;
  line-height: 1;
  margin-bottom: 20px;
}
.g-button.button {
  margin-right: 10px;
  background-color: transparent;
  border-radius: 2px;
  border: 1px solid #002B49;
  color: #002B49;
  display: inline-block;
  font-family: "Source Sans 3", sans-serif;
  font-size: 14px !important;
  font-weight: 600;
  margin-top: 1rem;
  letter-spacing: 1.5px;
  padding: 8px 34px 8px 20px;
  position: relative;
  text-transform: uppercase;
}
.g-button.button a {
  position: relative;
}
.g-button.button:before {
  font-family: "Font Awesome 6 Pro";
  content: "\f054";
  font-weight: 400;
  position: absolute;
  top: 8px;
  right: 14px;
  height: 30px;
  font-size: 14px;
  width: 8px;
}
.g-button.button i {
  margin-left: 14px;
}
.g-button.button:hover, .g-button.button.active {
  background-color: rgb(0, 21.9712328767, 37.3);
  color: #fff;
}
.g-image--top-left {
  border-radius: 1rem;
  position: relative;
  right: unset;
  left: unset;
  margin: auto;
  display: block;
  transition: all 0.5s ease-in-out;
}
@media screen and (min-width: 80em) {
  .g-image--top-left {
    position: absolute;
    top: 0;
    left: -7rem;
  }
}
@media screen and (min-width: 90em) {
  .g-image--top-left {
    position: absolute;
    top: 0;
    left: -16px;
  }
}
.g-image--top-right {
  position: relative;
  border-radius: 1rem;
  right: unset;
  left: unset;
  margin: auto;
  display: block;
  transition: all 0.5s ease-in-out;
}
@media screen and (min-width: 80em) {
  .g-image--top-right {
    position: absolute;
    bottom: 0;
    right: -7rem;
  }
}
@media screen and (min-width: 90em) {
  .g-image--top-right {
    right: -16px;
    position: absolute;
    bottom: 0;
  }
}